import { useState, useEffect } from "react";
import { ThemeProvider, createTheme } from "@mui/material/styles";
import CssBaseline from "@mui/material/CssBaseline";
import Box from "@mui/material/Box";
import Sidebar from "../components/Sidebar";
import themes from "./theme";
import '../styles/global.css';

function MyApp({ Component, pageProps }) {
  const [themeMode, setThemeMode] = useState("light"); // Default to light mode

  // Optional: Auto-detect theme based on time/weather (keep if needed)
  useEffect(() => {
    fetch("https://api.open-meteo.com/v1/forecast?latitude=28.6139&longitude=77.2090&current_weather=true")
      .then((res) => res.json())
      .then((data) => {
        const weather = data.current_weather.weathercode;
        if (weather >= 0 && weather < 3) setThemeMode("sunny");
        else if (weather >= 3 && weather < 6) setThemeMode("rainy");
        else if (weather >= 6 && weather < 8) setThemeMode("stormy");
        else setThemeMode("snowy");
      })
      .catch(() => setThemeMode("light")); // Fallback to light mode
  }, []);

  // Create theme with proper TypeScript support
  const muiTheme = createTheme({
    palette: {
      mode: themeMode === "dark" ? "dark" : "light",
      background: { 
        default: themes[themeMode]?.background || "#f5f5f5",
        paper: themes[themeMode]?.paper || "#ffffff",
      },
      text: { 
        primary: themes[themeMode]?.text || "#121212",
      },
      primary: {
        main: "#1976d2", // Customize as needed
      },
    },
    zIndex: {
      appBar: 1200, // Ensure Sidebar button (zIndex: 1300) stays above
    },
  });

  return (
    <ThemeProvider theme={muiTheme}>
    <CssBaseline />
    <Box sx={{ display: 'flex' }}>
      <Sidebar />
      
      {/* Main content area */}
      <Box 
        component="main"
        sx={{
          flexGrow: 1,
          p: 3,
          width: 'calc(100% - 270px)', // Adjust based on sidebar width
          maxWidth: 1200, // Set maximum content width
          margin: '0 auto', // Center the content
          transition: (theme) => theme.transitions.create('margin', {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.leavingScreen,
          }),
        }}
      >
        <Component {...pageProps} />
      </Box>
    </Box>
  </ThemeProvider>
  );
}

export default MyApp;